<header class="header"><div class="container"><div class="logo">ToDo</div><div class="gh-logo"><a class="gh-link" href="https://github.com/andrey-fanin/todo"><div class="gh-link__img"><img src="./assets/gh-logo.png" alt="github octocat logo"></div></a></div><div class="form"><input type="text" :placeholder="placeholder" v-model.trim="handleInput" @keypress.enter="addTask"><button class="btn" @click="addTask">Add new task</button></div></div></header><div class="container container--main"><div class="container container--mob"><transition name="rotateY"><div class="form form--mob" v-if="isActive"><textarea type="text" :placeholder="placeholder" v-model.trim="handleInput" @keypress.enter="addTask"></textarea><button class="btn" @click="addTask">+</button></div></transition><div class="buttons__wrap"><button class="btn btn--wide" @click="checkActive">{{ isActive ? 'close' : 'add new task' }}</button><transition name="smoothFade"><button class="btn btn--transparent" v-if="isActive" @click="clearHandleInput">clear text</button></transition></div></div><div class="buttons__wrap buttons__wrap--remove"><div><transition name="fadeX"><div v-if="todoList.length"><button class="btn btn-remove" @click="clearTodo">clear todo</button></div></transition></div><div><transition name="fadeX"><div v-if="doneList.length"><button class="btn btn-remove" @click="clearDone">clear done</button></div></transition></div></div><div class="tasks-title__wrap"><h2>To do</h2><span class="tasks-title__wrap-num" :class="{ active: getDoge }"><transition name="numberScale"><div :key="todoList.length">{{ todoList.length }}</div></transition></span></div><ul class="task-list"><li v-for="(task, idx) in todoList" :key="task.id"><label><input type="checkbox" @change="check(idx, 'active')"><div>{{ task.title }}</div></label><button class="btn btn-remove" @click="removeItem(idx, 'active')">remove</button></li></ul><transition name="jumpIn"><div class="doge--wrapper" v-if="isDoge" @click="checkDoge"><div class="doge--wrapper__img"><img src="./assets/doge.png" alt="super dog" title="puper dog"></div></div></transition><div class="tasks-title__wrap"><h2>Done</h2><span class="tasks-title__wrap-num"><transition name="numberScale"><div :key="doneList.length">{{ doneList.length }}</div></transition></span></div><ul class="task-list task-list--done"><li v-for="(task, idx) in doneList" :key="task.id"><label><input type="checkbox" checked @change="check(idx, 'completed')"><div>{{ task.title}}</div></label><button class="btn btn-remove" @click="removeItem(idx, 'completed')">remove</button></li></ul><div class="advices__wrap" v-if="showAdvice"><transition-group name="smoothFade"><div class="advices__wrap-emoji" v-html="emoji" :key="emoji"></div><p class="advices__wrap-text" :key="advice">{{ advice }}</p></transition-group></div></div><footer class="footer"><div class="container container--footer"></div></footer>